.progressBar{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 540px;
}
.progressBackground{
    width: 100%;
    height: 15px;
    background-color: #d7d7d7;
    border-radius: 10px;
}
.progressLine{
    height: 100%;
    background-color: #91cc7c;
    border-radius: 10px;
}
.titleProgress{
    font-size: 24px;
    font-style: normal;
    font-weight: 500;

    text-align: center;
}